
.active-item {
  background-color: #222;
  border-bottom: 1px #333 solid;
  box-sizing: border-box;
  color: #fff;
  display: inline-block;
  position: relative;
  transition: background 150ms;
  width: 100%;
}
.active-item:nth-child(even) {
    background-color: #000;
}
.active-item.current {
    background-color: aliceblue;
    color: black;
}
.active-item.chosen {
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
    -webkit-animation-duration: 700ms;
            animation-duration: 700ms;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-name: chosen;
            animation-name: chosen;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
}
.active-item.chosen.deletable {
    -webkit-animation-name: chosen-deletable;
            animation-name: chosen-deletable;
}
@-webkit-keyframes chosen {
from {
    background-color: aliceblue;
}
to {
    background-color: lime;
}
}
@keyframes chosen {
from {
    background-color: aliceblue;
}
to {
    background-color: lime;
}
}
@-webkit-keyframes chosen-deletable {
from {
    background-color: aliceblue;
}
to {
    background-color: red;
}
}
@keyframes chosen-deletable {
from {
    background-color: aliceblue;
}
to {
    background-color: red;
}
}
.active-item .field {
    margin-bottom: 0;
}
.active-item .field-label {
    text-align: left;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: end;
        -ms-flex-pack: end;
            justify-content: flex-end;
}
.active-item .field-body {
    -webkit-box-flex: 3;
        -ms-flex-positive: 3;
            flex-grow: 3;
}
.active-item input[type='range'] {
    vertical-align: middle;
}
.active-item .active-item-title {
    font-size: 1.6em;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    width: 100%;
    display: block;
    margin: 0;
}
.active-item .options label {
    font-size: 0.8em;
}
.active-item .handle-container {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.active-item .handle-container .handle {
    cursor: move;
    display: inline-block;
    text-align: center;
    vertical-align: middle;
}
.active-item .active-module-wrapper {
    padding: 8px;
    box-sizing: border-box;
    height: 135px;
}
@media screen and (min-width: 1023px) {
.active-item .dropdown .dropdown-content {
      height: 220px;
      overflow-y: scroll;
      overflow-x: hidden;
}
}



