/* Palette Control */
.swatch[data-v-b2b41748], .add-swatch[data-v-b2b41748] {
  border-radius: 50%;
  display: inline-block;
  height: 12px;
  width: 12px;
  margin: 3px;
  cursor: pointer;
  border: 2px solid #fff;
  transition: border-color 1200ms;
}
.swatch.current[data-v-b2b41748] {
  border-color: #000;
}
.add-swatch[data-v-b2b41748]::before {
  content: '+';
  line-height: 8px;
  display: inline-block;
  vertical-align: top;
  font-size: 14px;
  text-align: center;
  width: 100%;
  height: 100%;
}
.add-swatch input[data-v-b2b41748] {
  display: none;
}
.palette[data-v-b2b41748] {
  display: inline-block;
}
.pure-control-group input[data-v-b2b41748] {
  max-width: 129px;
}
input.pure-form-message-inline[data-v-b2b41748] {
  max-width: 70px;
}
.picker-wrapper[data-v-b2b41748] {
  text-align: right;
}
.picker-wrapper .vue-color__sketch[data-v-b2b41748] {
    display: inline-block;
    margin: 1em;
    text-align: left;
}
.customCheckbox label[data-v-b2b41748] {
  text-align: initial;
  display: initial;
  vertical-align: initial;
  width: 20px;
  margin: initial;
}



