
.status-bar {
  position: absolute;
  bottom: -35px;
  left: 0;
  right: 0;
  padding-left: 0.5rem;
  background-color: #c1c1c1;
  height: 35px;
}
.status-bar.tags {
    margin: 0;
}
.status-bar.tags:not(:last-child) {
      margin-bottom: 0;
}
.status-bar.tags .tag {
      margin-bottom: 0;
}



