
.slimui .module-list .options .opacity-group,
.slimui .module-list .options .blending-group {
  display: none;
}
.slimui .module-list .options .enable-group label {
  display: none;
}
.slimui .module-list .options .enable-group label.checkbox {
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
}
.slimui .module-list .options .control-group > label {
  min-width: 0;
}
.slimui .module-list .active-item .active-item-title {
  font-size: 1em;
  float: left;
  width: 100%;
}
.slimui .module-list .active-item .active-module-wrapper {
  height: 40px;
}
.slimui .module-list .active-item .fa-reorder {
  font-size: 1em;
}



