
html {
  overflow: hidden;
}
.active-list-wrapper {
  position: relative;
}
.active-list-wrapper > div {
  height: calc(100% - 38px);
}
#app {
  background-color: #303030;
  width: 100vw;
  height: calc(100vh - 35px);
  position: relative;
}
.control-panel-wrapper {
  padding: 10px;
}
section.section {
  height: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  padding: 0;
}
section.section > .columns {
  height: 100%;
}
section.section .top {
  height: 75%;
  max-height: calc(100% - 170px);
  min-height: 170px;
}
section.section .top > .columns {
  height: 100%;
}
section.section .top .active-list {
  min-height: 100%;
  box-sizing: border-box;
  position: relative;
  background-color: #0b0b09;
}
.active-list .gallery-item.sortable-ghost {
  height: 115px;
  width: 100%;
  overflow: hidden;
}
.active-list .gallery-item.sortable-ghost canvas {
  height: auto;
  position: relative;
  top: -10%;
}
section.section .top .active-list-wrapper {
  min-width: 306px;
}
section.section .top .active-list-wrapper,
.simplebar-content {
  height: 100%;
}
.bottom .bottom-inner {
  height: 100%;
  display: -webkit-box !important;
  display: -ms-flexbox !important;
  display: flex !important;
}
.vb > .vb-dragger {
  width: 12px;
  right: 0;
  text-align: center;
}
.vb > .vb-dragger > .vb-dragger-styler {
  width: 9px;
  transition: background-color 100ms ease-out, width 100ms ease-out, height 100ms ease-out;
  background-color: rgba(255, 166, 0, 0.6);
  border-radius: 20px;
  height: calc(100% - 10px);
  display: inline-block;
}
.vb.vb-scrolling-phantom > .vb-dragger > .vb-dragger-styler {
  background-color: rgba(255, 166, 0, 0.6);
}
.vb > .vb-dragger:hover > .vb-dragger-styler {
  background-color: #ffa600;
  width: 12px;
  height: 100%;
}
.vb.vb-dragging > .vb-dragger > .vb-dragger-styler {
  background-color: #ffa600;
  width: 12px;
  height: 100%;
}
.vb.vb-dragging-phantom > .vb-dragger > .vb-dragger-styler {
  background-color: #ffa600;
}
.layer-controls-wrapper {
  padding-left: 0;
}
.module-controls-wrapper {
  padding-right: 0;
}



